import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable, of } from "rxjs";
import { map } from "rxjs/operators";
import * as hmacsha1 from "hmacsha1";
import { SpotDetail } from "./class/SpotDetail";
import { SpotSimple } from "./class/SpotSimple";
import { SpotGroupType } from "./class/SpotGroupType";
import { SpotClass } from "./class/SpotClass";
import { RecommendFilterDto } from "./class/RecommendFilterDto";

@Injectable({
  providedIn: "root",
})
export class ApiService {
  constructor(private http: HttpClient) {}

  testApi(): Observable<any> {
    return this.getApiByUrl(
      `https://ptx.transportdata.tw/MOTC/v2/Tourism/ScenicSpot?$top=30&$format=JSON`
    );
  }

  public getSpotlight(): Observable<SpotSimple[]> {
    const filterName = [
      "C1_379000000A_000217",
      "C1_315081300H_000114",
      "C1_315081300H_000088",
      "C1_379000000A_000019",
      "C1_376480000A_000304",
      "C1_379000000A_000023",
      "C1_397000000A_000637",
      "C1_315081600H_000309",
    ];
    const filterParam = filterName.map((d) => `ID eq '${d}'`).join("or ");
    return this.getApiByUrl(
      `https://ptx.transportdata.tw/MOTC/v2/Tourism/ScenicSpot?$filter=${filterParam}&$format=JSON`
    ).pipe(map((res) => this.apiDtoToSpotSimple(res)));
  }

  public getRecommendFilterDto():Observable<RecommendFilterDto[]>{
    const selectParam = `$select=Class1,Picture`;
    const randomStart = Math.round(Math.random()*1000);
    return this.getApiByUrl(
      `https://ptx.transportdata.tw/MOTC/v2/Tourism/ScenicSpot?${selectParam}&$filter=Picture/PictureUrl1 ne null and Class1 ne null&$format=JSON&$top=6&$skip=${randomStart}`
    ).pipe(map(res=>(res.map(d=>{
      return{
        imgUrl: d.Picture ? d.Picture.PictureUrl1 : "",
        filterParam: this.apiClassToGroupType([d.Class1])[0],
      }
    }))))
    
  }

  public getSpotDetailById(id: string): Observable<SpotDetail> {
    return this.getApiByUrl(
      `https://ptx.transportdata.tw/MOTC/v2/Tourism/ScenicSpot?$filter=ID eq '${id}'&$format=JSON`
    ).pipe(map((res) => this.apiDtoToSpotDetail(res[0])));
  }
  private getApiByUrl(url: string): Observable<any> {
    return this.http.get(url, { headers: this.getHeaders() });
  }

  private apiDtoToSpotSimple(resData: Array<any>): SpotSimple[] {
    return resData.map((d) => {
      return {
        id: d.ID,
        name: d.Name,
        img: {
          url: d.Picture ? d.Picture.PictureUrl1 : "",
          title: d.Picture ? d.Picture.PictureDescription1 : "",
        },
        openTime: d.OpenTime,
        typeList: this.apiClassToGroupType([
          d.Class1,
          d.Class2,
          d.Class3,
        ] as Array<SpotClass | undefined>),
      };
    });
  }

  private apiDtoToSpotDetail(resData: any): SpotDetail {

      const imgUrl = [
        {
          url: resData.Picture ? resData.Picture.PictureUrl1 : "",
          title: resData.Picture ? resData.Picture.PictureDescription1 : "",
        },
        {
          url: resData.Picture ? resData.Picture.PictureUrl2 : "",
          title: resData.Picture ? resData.Picture.PictureDescription2 : "",
        },
        {
          url: resData.Picture ? resData.Picture.PictureUrl3 : "",
          title: resData.Picture ? resData.Picture.PictureDescription3 : "",
        },
      ];
      return {
        id: resData.ID,
        name: resData.Name,
        imgUrlList: imgUrl,
        city: resData.City,
        address: resData.Address,
        zipCode: resData.ZipCode,
        websiteUrl: resData.WebsiteUrl,
        descriptionDetail: resData.DescriptionDetail,
        phone: resData.Phone,
        updateTime: resData.UpdateTime,
        ticketInfo: resData.TicketInfo,
        position: {
          lon: resData.Position.PositionLon,
          lat: resData.Position.PositionLat,
        },
        parkingInfo: resData.ParkingInfo,
        openTime: resData.OpenTime,
        typeList: this.apiClassToGroupType([
          resData.Class1,
          resData.Class2,
          resData.Class3,
        ] as Array<SpotClass | undefined>),
      }
  }
  private getHeaders(): HttpHeaders {
    const gmtTime = new Date().toUTCString();
    const hash = hmacsha1("XnkO4Q6oiRxv8Q7LfXsO-E_ECug", `x-date: ${gmtTime}`);
    const headers: HttpHeaders = new HttpHeaders({
      "Content-Type": "application/json",
      Authorization: `hmac username="924170cd868348de90cf7b30f8cda570", algorithm="hmac-sha1", headers="x-date", signature="${hash}"`,
      "x-date": gmtTime,
    });
    return headers;
  }

  private apiClassToGroupType(
    classList: Array<SpotClass | undefined>
  ): SpotGroupType[] {
    const typeList = [
      {
        groupType: SpotGroupType.NATION,
        list: [
          SpotClass.NATURAL,
          SpotClass.METROPOLITAN_PARK,
          SpotClass.NATION_SCENE,
          SpotClass.FOREST,
          SpotClass.NATION_PARK,
        ],
      },
      {
        groupType: SpotGroupType.FARM,
        list: [
          SpotClass.ECOLOGICAL,
          SpotClass.RECREATION,
          SpotClass.TOURIST_FACTORY,
          SpotClass.FOREST_FARM,
          SpotClass.AGRICULTURAL_RECREATION,
        ],
      },
      {
        groupType: SpotGroupType.CULTURE,
        list: [
          SpotClass.CULTURE,
          SpotClass.TEMPLE,
          SpotClass.SPA,
          SpotClass.EAT,
          SpotClass.ART,
        ],
      },
      {
        groupType: SpotGroupType.OTHER,
        list: [SpotClass.SPORT, SpotClass.OTHER],
      },
    ];
    const newObject = {};
    classList.forEach((d) => {
      if (d) {
        const inGroup = typeList.find((typeDto) => typeDto.list.includes(d));
        console.log(inGroup, d);
        if (inGroup) {
          newObject[inGroup.groupType] = 1;
        } else {
          newObject[SpotGroupType.OTHER] = 1;
        }
      }
    });
    return Object.keys(newObject) as SpotGroupType[];
  }
}

